"use strict"

const Model = {

    bars : function() {
        var xhr = new XMLHttpRequest();
        xhr.open('GET', 'https://raw.githubusercontent.com/benbalter/dc-wifi-social/master/bars.geojson', false);
        xhr.send();
        if (xhr.status != 200) {
            // обработать ошибку
            console.log(xhr.status + ': ' + xhr.statusText); // пример вывода: 404: Not Found
        } else {
            // вывести результат
            return JSON.parse(xhr.response);
        }
    },
    metro:function() {
        var xhr = new XMLHttpRequest();
        xhr.open('GET', 'https://raw.githubusercontent.com/nextgis/metro4all/master/data/msk/portals.csv', false);
        xhr.send();
        if (xhr.status != 200) {
            // обработать ошибку
            console.log(xhr.status + ': ' + xhr.statusText); // пример вывода: 404: Not Found
        } else {
            // вывести результат
            return JSON.parse(xhr.response);
        }
    },
    some:function() {
        var xhr = new XMLHttpRequest();

        xhr.open('GET', 'some-layer.json', false);

        xhr.send();
        if (xhr.status != 200) {
            // обработать ошибку
            console.log(xhr.status + ': ' + xhr.statusText); // пример вывода: 404: Not Found
        } else {
            // вывести результат
            return JSON.parse(xhr.response);
        }
    }
};

export default Model;

